<template>
	<div class="container-fluid top56px">		
		
		<div class="bg-white border-right h-100 
					sidenav sidenav-show sidenav-show-up" 
			:style="{display:sidenavDis}"
		>
			<slot name="sidebar">
				sidebar
			</slot>
		</div>
			
		<div class="content"
			:style="{marginLeft:contentMarginLeft}"
		>
			<div class="pt-3">
			
			<b-button 
				:variant="'outline-'+clr"
				@click="sidebarDis"
				:class="btnCls"
			>
				<font-awesome-icon :icon="['fas', showSidebar?'angle-double-left':'angle-double-right']" />
			</b-button>	
			
			</div>
			
			<header>
				<slot name="header"></slot>
			</header>
				
			<main class='text-center'>
				<slot name="main">
					main
				</slot>
			</main>
				
			<footer>
				<slot name="footer">footer</slot>
			</footer>
		</div>
		
		<div :class="showDrop?'sidenav-backdrop':''" @click="backdropHide" ></div>
	</div>
		
	
	

</template>

<script>
import Vue from 'vue'

import { ButtonPlugin } from 'bootstrap-vue';
//Vue.use(ButtonPlugin);
new Vue('b-button',{ButtonPlugin});

import { mapState } from 'vuex'

export default {
  name: 'TheContainer',
  data:function(){
	return {
		showDrop:true,
		showSidebar:true,
		contentMarginLeft:'200px',
		sidenavDis:'block',
		btnCls:'font24px py-0 px-2',
	}
  },
	computed: mapState({
		clr:state=>state.activeNav.themeClr
	
  }),
  methods:{
	backdropHide:function(){
		this.showDrop=!this.showDrop
		return this.sidenavHide()
	},
	sidenavShow(){
		this.showSidebar=true
		this.contentMarginLeft='200px'
		this.sidenavDis='block'
		this.showDrop=true
		
	},
	sidenavHide(){
		this.showSidebar=false
		this.contentMarginLeft='0px'
		this.sidenavDis='none'
	},
	sidebarDis(){
		return this.showSidebar?this.sidenavHide():this.sidenavShow()
	}
  },
  props: {
    msg: String
  }
}
</script>

