<template>
<div>
	<div class="py-3 px-1 font18px border-top-0"
		:class="`${borderCls} text-${themeClr}`"
	>
		<font-awesome-icon :icon="sidebar.faIcon" />&nbsp;<span class="font-weight-bold">{{sidebar.caption}}</span>
	</div>


	<b-nav vertical>
		
		<!-- <b-nav-item variant="active">Active</b-nav-item> -->
		<!-- <b-nav-item>Link</b-nav-item> -->
		<!-- <b-nav-item disabled>Disabled</b-nav-item> -->
		
		<!-- 此div的作用是给cat里的各个项加框线 -->
		<div v-for="(cat,catId) in sidebar.catArr" 
			:class="cat.name?`${borderCls} border-top-0`:`border-bottom-0`"
			:key="catId"
		>
			<!-- 此div的作用是给button扩充外围空间，当button点击时不与外围的框线重合-->
			<div class="pb-1 mt-1"
				v-if="cat.name"
			>
			<b-button 
				block
				squared
				class="font18px text-left border-0"
				:class="`${bsvAlignTwoEnds}`"
				v-b-toggle="`collapse-${catId}`"
				:variant="`outline-${themeClr}`"
			>
				<span class="font-italic">{{cat.caption}} </span>
				
				<span class="when-opened"><font-awesome-icon :icon="['fas','minus']" /></span> 
				<span class="when-closed"><font-awesome-icon :icon="['fas','chevron-down']" /></span>
				
			</b-button>
			</div>
			
			<b-collapse 
				:id="`collapse-${catId}`"
				:visible="cat.name?null:true"
			>
				<b-list-group>
					<!-- 此div的作用是给b-list-group-item加框线-->
					<div class="pt-1 pb-1"
						v-for="(ent, idx) in cat.sysEntArr"
						:key="idx"
						:class="cat.name?`ml-3 border-bottom-0 ${borderCls}`:`border-top-0 ${borderCls}`"
						:style="{width:cat.name?`180px`:null}"
					>
					
					<b-list-group-item 
						button
						class="py-1 rounded-0 border-0"
						:class="`${bsvAlignTwoEnds} text-${themeClr}`"
					>
						{{ ent.caption }}
						<b-badge pill
							:variant="themeClr" 
							v-if="idx"
						>{{idx}}
						</b-badge>
						<span v-else class="text-muted">无</span>
					</b-list-group-item>
					</div>

				</b-list-group>
			</b-collapse>
		</div>
		
	</b-nav>
</div>
</template>

<script>
/*
import Vue from 'vue'

import { NavPlugin } from 'bootstrap-vue'
Vue.use('b-nav',{NavPlugin})

import { CollapsePlugin } from 'bootstrap-vue'
Vue.use('b-collapse',{CollapsePlugin})
*/

import { mapState } from 'vuex'

export default {
	name: 'TheSidebar',
	data(){
		return {
			//bsv中的"d-flex justify-content-between align-items-center"是对button中的内容进行两端对齐
			bsvAlignTwoEnds:"d-flex justify-content-between align-items-center"
		}
	},
	computed:{
		borderCls(){
			return `border border-left-0 border-right-0 border-${this.themeClr}`
		},
		...mapState({
		//activeNav对应的sidebar内容
			sidebar:state => {
				let ent=state.activeNav.name
				let arr=state.htmlItems.sidebar
				let sbObj={}
			
				for(let obj of arr){
					if(obj.name==ent){
						sbObj=obj
						break
					}
				}
				return sbObj
			},
			themeClr:state=>state.activeNav.themeClr,
			
		}),
	}
	//methods:{
		
	//},
	
}
</script>


<style scoped>

.font18px{
	font-size:18px
}

 
.collapsed > .when-opened,
:not(.collapsed) > .when-closed {
  display: none;
}

/*
.collapsed > .when-opened{
  display: none;
}

:not(.collapsed) > .when-closed {
  display: none;
}
*/
</style>
