<template>
<div>
  <b-navbar fixed="top" toggleable="md" type="dark" variant="info">
    <b-navbar-brand href="#">
		<font-awesome-icon :icon="['fab', 'font-awesome']" />
	</b-navbar-brand>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav>
		<template v-for="(nav, idx) in navArr">
			<b-nav-item 
				v-if="nav.routeName !== 'ent'"
				:key="idx"
				@click="changeTable({
					table:{title:nav.caption,fieldLang:nav.fieldLang},
					fetchOption:{routeName:nav.routeName},
					activeNav:{name:nav.name}
				})" 
			>
				{{ nav.caption }}
			</b-nav-item>
		
			<b-nav-item-dropdown 
				v-else
				:key="idx"
				text="Entity"
			>
				<template v-for="(ent, idx) in entArr">
					<b-dropdown-item 
						:key="idx"
						@click="changeTable({
							table:{title:ent.caption,fieldLang:ent.fieldLang},
							fetchOption:{routeName:ent.routeName},
							activeNav:{name:ent.name}
						})"						
					>
						{{ ent.caption }}
					</b-dropdown-item>
				</template>
			
			</b-nav-item-dropdown>
		</template>		
      </b-navbar-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">
        <b-nav-form>
          <b-form-input size="sm" class="mr-sm-2" placeholder="Search"></b-form-input>
          <b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button>
        </b-nav-form>

        <b-nav-item-dropdown text="Lang" right>
          <b-dropdown-item href="#">EN</b-dropdown-item>
          <b-dropdown-item href="#">ES</b-dropdown-item>
          <b-dropdown-item href="#">RU</b-dropdown-item>
          <b-dropdown-item href="#">FA</b-dropdown-item>
        </b-nav-item-dropdown>

        <b-nav-item-dropdown right>
          <!-- Using 'button-content' slot -->
          <template v-slot:button-content>
            <em>User</em>
          </template>
			<b-dropdown-item href="#">Profile</b-dropdown-item>
			<b-dropdown-item href="#">
				<font-awesome-icon :icon="['fas', 'sign-out-alt']" />
				Sign Out
			</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</div>
</template>

<script>
/*
import Vue from 'vue'

import { NavbarPlugin } from 'bootstrap-vue'
//Vue.use(NavbarPlugin)
new Vue('b-navbar',{NavbarPlugin})
*/

import { mapState,mapActions } from 'vuex'

export default {
	name: 'TheNavbar',
	/* data(){
		return {
			navArr:navItems,
			entArr:entItems
		}
	}, */
	
	computed: mapState({
		navArr: state => state.htmlItems.nav,
		entArr: state => state.htmlItems.ent,
		clr : state=>state.activeNav.themeClr
	}),
	methods: {
		... mapActions({
			changeTable: 'asyChangeTable'
		})
	},
	
}
</script>


<style >


</style>
