<template>
	<div class="overflow-auto">
		
		<div class="text-left">
		
		<b-table striped hover caption-top
			id="my-talbe"
			:busy="table.isBusy"
			:items="table.items"
			:per-page="perPage"
			:current-page="currentPage"
			small
		>
			<template slot="table-caption">
				<h4 class="text-center" :class="'text-'+clr">{{table.title}}</h4>
			</template>
			
			<template slot="table-busy">
				<div class="text-center text-muted my-2">
					<b-spinner class="align-middle"></b-spinner>
					<strong> 接收数据中...</strong>
				</div>
			</template>
		</b-table>
		
		
		<div class="offset-9 text-right" v-show="rows > perPage" >
		<!-- <p class=""> -->
			<span>Total items: {{ rows }},</span>&nbsp;
			<span>Current Page: {{ currentPage }}</span>
			<br>
			<!-- <span>每页行数:</span> -->
			<label>每页行数:</label>
			<b-form-select v-model="perPage" :options="options"></b-form-select>	
		<!-- </p> -->
		
		</div>
		<br>
		<b-pagination
			v-model="currentPage"
			v-show="rows > perPage"
			align="right"
			:total-rows="rows"
			:per-page="perPage"
			aria-controls="my-table"
		></b-pagination>
		
		</div>
		
		
	</div>
</template>

<script>

import { mapState, mapActions } from 'vuex'

//commonJs Module
//module.exports= {
//native JS Module export
export default {
    name: 'TheTable',
    data:function() {
		return {
		//表格表头设置
		//fields:()={
		//	return
		//},
			perPage: 10,
			currentPage: 1,
			options: [
				{ value: 5, text: '5' },
				{ value: 10, text: '10' },
				{ value: 20, text: '20' },
				{ value: 50, text: '50', }
			]
		}
	},
	computed: mapState({
		table: state => state.table,		
		rows: state => state.table.items.length,
		clr : state=>state.activeNav.themeClr
	}),
	methods: {
		... mapActions({
			getTableItemsBy: 'asyChangeTable'
		})
	},
	//computed:{
		
		//...mapState({
			
		//})
	//},
	components:{
		//BButtonGroup,
		//BButton
	}
}

</script>

